
// „É´„Éº„É´ÁÆ°ÁêÜ„ÇØ„É©„Çπ„ÅÆ„Ç§„É≥„Éù„Éº„Éà
import { Rulelist } from './Rulelist'

/**
 * „É™„Çπ„ÉàÁï™Âè∑„Çí„Éï„Ç©„Éº„Éû„ÉÉ„ÉàÂåñ„Åô„Çã
 * @param longtext textareaÂÜÖ„ÅÆÊñáÂ≠óÂàó
 */
const listNumFormat = (longText: string) => {
    const oldLines = nSplit(longText);
    let count = new Array<number>(1).fill(0);
    // „Åù„ÅÆË°å„Å´„É™„Çπ„ÉàÁï™Âè∑„Åå„ÅÇ„Çã:„Ç´„Ç¶„É≥„Éà„Å´Âü∫„Å•„ÅçÁΩÆÊèõ
    // „Åù„ÅÆË°å„Å´„É™„Çπ„ÉàÁï™Âè∑„ÅØ„Å™„ÅÑ:‰Ωï„ÇÇ„Åó„Å™„ÅÑ
    // „Åù„ÅÆË°å„ÅÆÊñáÂ≠óÂàó„ÅØ0:„Ç´„Ç¶„É≥„Éà„Çí„É™„Çª„ÉÉ„Éà
    // „Çø„Éñ„Åå‰∏ä„Åå„Çã„ÄÅ„Åæ„Åü„ÅØÊîπË°å„Åå„ÅÇ„Çå„Å∞„É™„Çπ„Éà„Çí„É™„Çª„ÉÉ„Éà
    // „Çø„Éñ„Åå„ÅÑ„Åè„Å§„ÅÇ„Çã„Åã‚Üí„Åù„ÅÆÊï∞ÂàÜÊã°Âºµ
    // „Éç„Çπ„Éà„ÅÆÊ∑±„Åï„ÅÆÂ∑Æ„ÅØÂ∑¶ÂÅ¥„ÅÆÁ©∫ÁôΩÊï∞„Å´‰æùÂ≠ò
    const newLines = oldLines.map(line => {
        let l = "";

        // „É™„Çπ„ÉàÁï™Âè∑„Å´„Éû„ÉÉ„ÉÅ„Åô„ÇãÊ≠£Ë¶èË°®Áèæ„Ç™„Éñ„Ç∏„Çß„ÇØ„Éà
        const listNumMatch = new RegExp(/\d\./);

        // len : ÊúâÂäπ„Å™ÊñáÂ≠óÊï∞
        const len = line.trimStart().length;

        // nest : „Éç„Çπ„Éà„ÅÆÊ∑±„Åï
        const nest = line.length - len;
        // Ê∑±„Åï„Å´ÂØæÂøú„Åô„Çãcount„Åå„ÅÇ„Çã„ÅãÁ¢∫Ë™ç
        if (count.length <= nest) {
            // ÁÑ°„Åë„Çå„Å∞„Éç„Çπ„ÉàÂ∑ÆÂàÜ„ÅÆË¶ÅÁ¥†„ÇíËøΩÂä†(0)
            const luck = nest - count.length +1;
            const addArr = new Array<number>(luck).fill(0);
            // Ê∑±„Åï„Å´ÂØæÂøú„Åó„ÅüÊñ∞„Åó„ÅÑ„Ç´„Ç¶„É≥„ÉàÈÖçÂàó„ÇíÁî®ÊÑè
            count = count.concat(addArr);
        }
        //---------------------------------------------
        if (listNumMatch.test(line)) {
            // „Åù„ÅÆË°å„Å´„É™„Çπ„ÉàÁï™Âè∑„Åå„ÅÇ„ÇãÂ†¥Âêà
            // ÂØæÂøú„Åô„Çã„Ç´„Ç¶„É≥„Éà+1
            count[nest] += 1;

            // „Åù„Çå„Çà„ÇäÊ∑±„ÅÑ„Éç„Çπ„Éà„ÅØÂàùÊúüÂåñ
            count = count.fill(0, nest+1);

            // Ë°®Á§∫„Åô„Çã„É™„Çπ„ÉàÁï™Âè∑ÊñáÂ≠óÂàó
            const c = count[nest].toString();

            // „É™„Çπ„ÉàÁï™Âè∑„ÇíÁΩÆÊèõ„ÅóÊñ∞„Åü„Å™Ë°å„Å∏
            l = line.replace(listNumMatch, c + ".");
        } else if (len > 0) {
            // „É™„Çπ„ÉàÁï™Âè∑„ÅØ„Å™„ÅÑ„Åå„ÄÅ
            // Á©∫ÊñáÂ≠ó„Åß„ÅØ„Å™„ÅÑÂ†¥Âêà(ÊúâÂäπÊñáÂ≠óÊï∞„Åå0‰ª•‰∏ä)
            // ‰Ωï„ÇÇÁΩÆÊèõ„Åó„Å™„ÅÑ„ÄÅ„Ç´„Ç¶„É≥„Éà„ÇÇ‰øùÊåÅ
            // „Åü„Å†„Åó„ÄÅ„Åì„ÅÆ„Ç±„Éº„Çπ„Å†„Å®„ÄÅÊîπË°å„Åó„Å™„Åë„Çå„Å∞„ÄÅÂêå„Åò„Éç„Çπ„Éà„ÅØ„Åö„Å£„Å®ÈÄ£Áï™„Å®„Å™„Çã
            l = line;
        } else {
            // Á©∫ÊñáÂ≠ó„ÅÆÂ†¥Âêà(„Åä„Åù„Çâ„ÅèÊîπË°å)
            // ‰Ωï„ÇÇÁΩÆÊèõ„Åó„Å™„ÅÑ
            // „Åì„ÅÆË°å„ÅÆ„Éç„Çπ„Éà„Äå‰ª•‰∏ä„Äç„ÅÆ„Éç„Çπ„Éà„Å´„Å§„ÅÑ„Å¶„ÄÅ„Ç´„Ç¶„É≥„Éà„ÇíÂàùÊúüÂåñ„Åô„Çã
            // „É™„Çπ„ÉàÁï™Âè∑„ÅÇ„Çä„Å®„ÅØÊù°‰ª∂„ÅåÁï∞„Å™„Çã
            count = count.fill(0, nest);
            l = line;
        }
        return l;
    });
    const newLongText = nConcat(newLines);
    return newLongText;
}


/**
 * h1,h2,h3„Å´Ë©≤ÂΩì„Åô„Çã`#`„ÇíÂâäÈô§„Åó„ÄÅ„Åù„Çå„Å´Ë¶ãÂêà„ÅÜTab„ÇíË£úÊ≠£„Åô„Çã
 * @param longText textareaÂÜÖ„ÅÆÊñáÂ≠óÂàó
 * @return Êñ∞„Åó„ÅÑ„ÉÜ„Ç≠„Çπ„Éà
 */
const sharpFormat = (longText: string) => {
    const oldlines = nSplit(longText);
    // 1Ë°å„Åî„Å®„Å´Á¢∫Ë™ç
    // „ÇÇ„Åó###„Åå„ÅÇ„Çã‚ÜíË©≤ÂΩìË°å„Å®„Åù„Çå‰ª•Èôç„ÅØh3„Ç∞„É´„Éº„Éó(Ë¶ãÂá∫„Åótab2+Êú¨Êñátab3)
    // „ÇÇ„Åó##„Åå„ÅÇ„Çã‚ÜíË©≤ÂΩìË°å„Å®„Åù„Çå‰ª•Èôç„ÅØh2„Ç∞„É´„Éº„Éó(Ë¶ãÂá∫„Åótab1+Êú¨Êñátab1)
    // „ÇÇ„Åó#„Åå„ÅÇ„Çã‚ÜíË©≤ÂΩìË°å„Å®„Åù„Çå‰ª•Èôç„ÅØh1„Ç∞„É´„Éº„Éó(Ë¶ãÂá∫„Åótab0+Êú¨Êñátab1)
    // #„ÅåÁÑ°„ÅÑ‚ÜíÂâçË°å„ÅÆÁä∂ÊÖã„ÇíÁ∂ôÁ∂ö
    let headerNumber = 0;
    const newlines = oldlines.map(line => {
        let l :string;
        if (line.indexOf("\#\#\# ") >= 0) {
            // h3„Ç∞„É´„Éº„Éó
            headerNumber = 2;
            l = line.replace("### ", "\n\t\t");
        } else if (line.indexOf("\#\#") >= 0) {
            // h2„Ç∞„É´„Éº„Éó
            headerNumber = 1;
            l = line.replace("## ", "\n\t");
        } else if (line.indexOf("\#") >= 0) {
            // h1„Ç∞„É´„Éº„Éó
            headerNumber = 0;
            l = line.replace("# ", "\n");
        } else {
            // ÂâçË°å„ÅÆÁä∂ÊÖã„ÇíÁ∂ôÁ∂ö
            // headerNumber„ÅØÂ§âÊõ¥„Åó„Å™„ÅÑ
            // headNumberÂàÜ„ÅÆTab„Çíline„ÅÆÂâç„Å´‰ªò„Åë„Çã
            l = "\t".repeat(headerNumber) + line;
        }
        return l;
    });
    const newLongText = nConcat(newlines);
    return newLongText;
}

/**
 * []„Çí‚óè‚óè„Å´Â§âÊõ¥„Åô„Çã
 * @param longText
 * @return Êñ∞„Åó„ÅÑ„ÉÜ„Ç≠„Çπ„Éà
 */
const tag2maru = (longText: string) => {
    const oldlines = nSplit(longText);
    const newlines = oldlines.map(line => {
        const replaced = line.replace(new RegExp(/\[(.*)\]/), "‚óè$1‚óè\n");
        return replaced;
    });
    const newLongText = nConcat(newlines);
    return newLongText
}

/**
 * @summary "- " „Çí "„Éª\t"„Å´ÁΩÆÊèõ„Åô„Çã
 * @param longText textareaÂÜÖ„ÅÆÊñáÂ≠óÂàó
 * @returns Êñ∞„Åó„ÅÑ„ÉÜ„Ç≠„Çπ„Éà
 */
const hyphen2Circle = (longText: string) => {
    const oldlines = nSplit(longText);
    const newlines = oldlines.map(line => {
        return line.replace("- ", "„Éª\t");
    });
    const newLongText = nConcat(newlines);
    return newLongText;
}


/**
 * @summary ÂçäËßí„Çπ„Éö„Éº„Çπ4„Å§„ÇíTAB„Å´Â§âÊèõ„Åô„Çã„ÄÇ
 * @param longText textareaÂÜÖ„ÅÆÈï∑Êñá
 * @returns Êñ∞„Åó„ÅÑ„ÉÜ„Ç≠„Çπ„Éà
 * @description ÊúÄÂàù„Å´Âá∫„Å¶„Åç„Åü„Çπ„Éö„Éº„Çπ„ÅÆ„ÅøÁΩÆÊèõ„Åô„Çã
 */
const space2Tab = (longText: string) => {
    const oldlines = nSplit(longText);
    const newlines = oldlines.map(line => {
        const spans = spaceSplit(line);
        const newline = tConcat(spans);
        return newline;
    });
    const newLongText = nConcat(newlines);
    return newLongText;
};

/**
 * 
 * @param lineText 1Ë°å
 * @returns ÂàÜÂâ≤„Åï„Çå„ÅüË°å„ÅÆÊñ≠ÁâáÈÖçÂàó
 */
const spaceSplit = (lineText: string) => {
    return lineText.split("    ");
};

/**
 * @summary ÊñáÂ≠óÂàóÈÖçÂàó„ÅÆÂÖ®Ë¶ÅÁ¥†„Çí„Çø„Éñ„ÅßÁµêÂêà„Åô„Çã
 * @param spans ÊñáÂ≠óÂàóÈÖçÂàó
 * @returns ÁµêÂêà„Åï„Çå„ÅüË°åÊñáÂ≠óÂàó
 */
const tConcat = (spans: string[]) => {
    const longText = spans.join('\t');
    return longText;
};


/**
 * @summary ÂÖ±ÈÄöÂá¶ÁêÜ„ÄÅÂÖ•ÂäõÊñáÂ≠óÂàó„ÇíÊîπË°å„Ç≥„Éº„Éâ„Åß„Å∞„Çâ„Åô
 * @param longText Èï∑„ÅÑÊñáÂ≠óÂàó
 * @returns 1Ë°å„Åî„Å®„Å´ÂàÜ„Åã„Çå„ÅüÊñáÂ≠óÂàó
 */
const nSplit = (longText: string) => {
    return longText.split("\n");
};

/**
 * @summary ÂÖ±ÈÄöÂá¶ÁêÜ„ÄÅÂÖ•ÂäõÊñáÂ≠óÂàóÈÖçÂàó„ÇíÊîπË°å„Ç≥„Éº„Éâ„ÅßÈÄ£Áµê„Åô„Çã
 * @param lines 1Ë°å„Åî„Å®„ÅÆÊñáÂ≠óÂàóÈÖçÂàó
 * @returns 1Ë°å„Å´„Åæ„Å®„ÇÅ„Çâ„Çå„ÅüÊñáÂ≠óÂàó
 */
const nConcat = (lines: string[]) => {
    let longText = lines.join("\n");
    return longText;
};



/**
 * @summary „É´„Éº„É´„Å´Âæì„ÅÑ„ÉÜ„Ç≠„Çπ„Éà„ÇíÂ§âÊèõ„Åô„Çã
 * @param longText
 * @returns ÁΩÆÊèõÂæå„ÅÆ„ÉÜ„Ç≠„Çπ„Éà
 */
const convert = (longText: string) => {

    // „É´„Éº„É´„É™„Çπ„Éà„ÅÆ‰ΩúÊàê
    const rl = new Rulelist;

    // ÂêÑÁ®Æ„É´„Éº„É´„ÅÆÈÅ©Áî®
    rl.setRule({
        primary: 5,
        func: space2Tab
    });
    rl.setRule({
        primary: 10,
        func: hyphen2Circle
    });
    rl.setRule({
        primary: 10,
        func: tag2maru
    });
    rl.setRule({
        primary: 20,
        func: sharpFormat
    });
    rl.setRule({
        primary: 25,
        func:listNumFormat
    })

    // „É´„Éº„É´„Å´Âü∫„Å•„ÅçÂÆüË°å
    const result = rl.doAll(longText);
    return result;
};

export {
    convert,
    space2Tab,
    hyphen2Circle,
    tag2maru,
    sharpFormat,
    listNumFormat,
    spaceSplit,
    tConcat,
    nSplit,
    nConcat
};