
/**
 * @interface Rule „É´„Éº„É´„Ç§„É≥„Çø„Éº„Éï„Çß„Éº„Çπ
 * @description „É´„Éº„É´ÁÆ°ÁêÜ„ÇØ„É©„Çπ„ÅåÊ†ºÁ¥ç„Åô„Çã„É´„Éº„É´„ÅÆÊåÅ„Å§ÂÜÖÂÆπ
 */
interface Rule {
    // primary„ÅØÂ∞è„Åï„ÅÑ„Åª„Å©ÂÖà„Å´ÂÆüË°å„Åï„Çå„Çã
    primary: number;
    func: Function;
}

/**
 * @class Rulelist
 * @description „É´„Éº„É´ÁÆ°ÁêÜ„ÇØ„É©„Çπ
 * @member rules „É´„Éº„É´ÈÖçÂàó
 * @constructor
 * @method setRule „É´„Éº„É´„ÇíËøΩÂä†
 * @method doAll ÁÆ°ÁêÜ„Åó„Å¶„ÅÑ„Çã„É´„Éº„É´„ÇíÂÖ®„Å¶ÂÆüË°å
 */
class Rulelist{
    /**
     * @member rules
     * „É´„Éº„É´ÁÆ°ÁêÜ„ÇØ„É©„Çπ„ÅÆÁÆ°ÁêÜ„Åô„Çã„É´„Éº„É´Array
     */
    private rules: Array<Rule>;

    /**
     * @constructor „É´„Éº„É´ÈÖçÂàó„ÇíÂàùÊúüÂåñ
     */
    constructor() {
        // „É´„Éº„É´„Ç§„É≥„Çπ„Çø„É≥„Çπ‰ΩúÊàêÊôÇ„Å´ÂàùÊúüÂåñ
        this.rules = new Array;
    }
    
    /**
     * @method setRule
     * @description „É´„Éº„É´„ÇíÊñ∞„Åü„Å´ËøΩÂä†„Åô„Çã
     * @param {Rule} param „É´„Éº„É´
     */
    public setRule(param: Rule) {
        this.rules.push(param);
        this.sortRules();
    }

    /**
     * @method doAll
     * @param {String} longText Â§âÊèõÂØæË±°„ÉÜ„Ç≠„Çπ„Éà
     * @returns {String} Â§âÊèõÂæå„ÅÆ„ÉÜ„Ç≠„Çπ„Éà
     */
    public doAll(longText: string) : string {
        this.rules.forEach(rule => {
            longText = rule.func(longText)
        });
        console.log(longText);
        return longText;
    }
    /**
     * @method sortRules
     * @description Rulelist„ÅÆÁÆ°ÁêÜ„Åô„Çã„É´„Éº„É´„Çí‰∏¶„ÅπÊõø„Åà„Çã„ÄÇ
     * ÂºïÊï∞„ÄÅÊàª„ÇäÂÄ§„Å™„Åó
     */
    private sortRules() {
        this.rules.sort((a, b) => {
            return a.primary - b.primary;
        });
    }
}

export {
    Rulelist
}